.h-100 {
  height: 100%;
}

.btn {
  background-color: inherit;
  border-radius: 3px;
  padding: 5px 10px;
  border: 1px solid #01579b;
  border: 1px solid var(--secondary-bg-1);
  color: #fafafa;
  color: var(--primary-label);
}

header.nav-header {
  width: 100%;
  padding: 10px;
  background-color: #252525;
  background-color: var(--primary-bg-1);
  position: -webkit-sticky;
  position: sticky; /* Set the navbar to fixed position */
  top: 0;
  z-index: 1000;
}

.navbar {
  flex: 6 1;
}

.navbar ul {
  list-style: none;
  align-items: center;
}

.nav-Link {
  padding: 10px;
  border-radius: 4px;
}

.nav-Link:hover {
  background-color: #2c2c2c;
  background-color: var(--primary-bg);
  color: #fafafa;
  color: var(--primary-label);
}

.nav-item .nav-Link {
  text-decoration: none;
  color: #cccaca;
  color: var(--primary-label-1);
  font-weight: 300;
  letter-spacing: 1px;
}

.nav-item.active .nav-Link {
  color: #fafafa !important;
  color: var(--primary-label) !important;
  border: 1px solid #039be5;
  border: 1px solid var(--secondary-label-2);
  border-radius: 4px;
}

.nav-item:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.header-title {
  align-items: center;
  flex: 2 1;
  padding-left: 10px;
  font-size: 25px;
  font-weight: 400;
  color: #039be5;
  color: var(--secondary-label-2);
  letter-spacing: 1px;
}

.search-control {
  margin-bottom: 3px;
}
.search-control input {
  height: 30px;
  margin-right: 7px;
}

.search-control .btn {
  height: 30px;
  cursor: pointer;
}

footer {
  background-color: #252525;
  background-color: var(--primary-bg-1);
  color: #cccaca;
  color: var(--primary-label-1);
  min-height: 120px;
  position: relative;
  bottom: 0px;
  margin-top: 30px;
  padding-bottom: 35px;
}

.footer-links {
  list-style: none;
