}

.text-description {
  font-size: 18px;
  font-weight: 400;
  word-wrap: break-word;
  text-align: justify;
}

.logo {
  text-decoration: none;
  color: #039be5 !important;
  color: var(--secondary-label-2) !important;
}

/* Fading animation */
.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

@-webkit-keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

@keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

body {
  overflow-x: hidden;
}

.text-align-center {
  text-align: center !important;
}

.cursor-pointer {
  cursor: pointer;
}


.carousel-container {
  margin-top: 30px;
}

.carousel {
  margin: 20px;
  width: 90%;
  padding-left: 25px;
  border-radius: 7px;
