  background: inherit;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  box-shadow: inset 0 0 2000px rgba(124, 124, 124, 0.5);
  -webkit-filter: blur(10px);
          filter: blur(10px);
  z-index: -1;
}

.display_message{
  display: flex;
  flex-direction: column;
    align-items: flex-end;
    width: 89%;
  max-height: 380px;
  overflow-y: scroll;
  flex: 3 1;
  padding-right: 50px;
}

.display_message p {
  order: 1px solid #40c4ff;
  order: 1px solid var(--secondary-bg-2);
    padding: 10px;
    border-radius: 15px;
    background-color: white;
    color: black;
    width: -webkit-fit-content !important;
    width: -moz-fit-content !important;
    width: fit-content !important;
}

.chatBox {
  height: 50px;
  width: 70%;
}

.chatBox textarea,
.chatBox button {
  display: flex;
  align-items: center;
  font-size: 16px;
  height: 50px;
}

.chatBox button {
  justify-content: center;
}

.chat-area {
  flex-direction: column-reverse;
  align-items: flex-end;
  flex: 1 1;
  height: 50px;
  max-height: 50px;
}

.user-dashboard-container {
  height: calc(100vh - 195px);
}

.side-bar-navigation {
  flex-direction: column;
  height: calc(100vh - 50px);
  position: absolute;
  width: 280px;
  background-color: #ffffff;
  background-color: var(--color-light);
  box-shadow: 0px 0px 3px 2px #cccaca;
  box-shadow: 0px 0px 3px 2px var(--primary-label-1);
  display: none;
  z-index: 2000000;
  top: 57px;
}

.side-bar-navigation ul {
  display: flex;
  flex-direction: column;
  list-style: none;
}

.side-bar-navigation li {
  font-size: 28px;
  padding: 20px;
  color: #2c2c2c !important;
  color: var(--primary-bg) !important;
  font-family: 'Roboto', 'sans-seriff';
  font-weight: 300;
}

.side-bar-navigation a {
  color: #2c2c2c !important;
  color: var(--primary-bg) !important;
}


