body {
    font-family: Arial;
    font-size: 17px;
    color: black;
    padding: 8px;
  }
  
  * {
    box-sizing: border-box;
  }
  
  .row { /* IE10 */
    display: flex; /* IE10 */
    flex-wrap: wrap;
    margin: 0 -16px;
  }
  
  .col-25 { /* IE10 */
    flex: 25% 1;
  }
  
  .col-50 { /* IE10 */
    flex: 50% 1;
  }
  
  .col-75 { /* IE10 */
    flex: 75% 1;
  }
  
  .col-25,
  .col-50,
  .col-75 {
    padding: 0 16px;
  }
  
  .container {
    background-color: aliceblue;
    color: #757575;
    padding: 5px 20px 15px 20px;
    border: 1px solid lightgrey;
    border-radius: 3px;
  }
  
  input[type=text] {
    width: 100%;
    margin-bottom: 20px;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 3px;
  }
  
  label {
    margin-bottom: 10px;
    display: block;
  }
  
  .icon-container {
    margin-bottom: 20px;
    padding: 7px 0;
    font-size: 24px;
  }
  
  .btn {
    background-color: deepskyblue;
    color: white;
    padding: 12px;
    margin: auto;
    border: none;
    width: 50%;
    border-radius: 3px;
    cursor: pointer;
    font-size: 17px;
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column-reverse;
    justify-content: space-between;
    align-items: center;
  }
  
  .btn:hover {
    background-color: #106fdb;
  }
  
  .Cart{
